body {
    background-color: #013b0e;
    background-image: url(/solitaire/static/media/felt.5b148bae.png);
    /*https://www.transparenttextures.com/felt.html This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

button {
    padding: 0;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
}

.jail {
    
}

.ui {
    z-index: 2000;
}

.ui > .content > .section {
    font-size: 1vw;
    margin-bottom: 8vw;
}

.ui > .content > .section:last-child {
    margin-bottom: 0vw;
}

.ui > .content > .section > .title {
    font-size: 1.5vw;
    width: 25vw;
    float: left;
}

.ui > .content > .section > .row > select {
    float: right;
    margin-left: 2vw;
    width: 8vw;
    font-size: 1vw;
}

.ui > .content > .section > .row > .label {
    float: left;
    width: 15vw;
    font-size: 1w;
    color: lightgray;
}

.ui > .content > .section > .row {
    clear: both;
}

.ui.neutral {
    background-color: rgba(0, 0, 0, 0.95);
    color: #fff;
    padding: 2vw;
    border-radius: 0.5vw;
    z-index: 5000;
    width: 30vw;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.appwrapper-16-to-9-portrait {
    margin: auto;
    overflow: none;
    box-sizing: border-box;
    width: 100vw;
    max-height: 100vh;
}
.appwrapper-16-to-9-landscape {
    margin: auto;
    overflow: none;
    box-sizing: border-box;
    width: 100vw;
    height: 56.25vw; /* height:width ratio = 9/16 = .5625  */
    max-height: 100vh;
    max-width: 177.78vh; /* 16/9 = 1.778 */
}
.appwrapper-16-to-9-landscape-inner {
    width: 100%;
    padding-bottom: 56.25%;
    font-size: 1.778vh;
    position: relative;
}
.appwrapper-16-to-9-portrait-inner {
    width: 100%;
    padding-bottom: 56.25%;
    font-size: 1vw;
    position: relative;
}

.appwrapper-4-to-3-portrait {
    margin: auto;
    overflow: none;
    box-sizing: border-box;
    width: 100vw;
    max-height: 100vh;
}
.appwrapper-4-to-3-landscape {
    margin: auto;
    overflow: none;
    box-sizing: border-box;
    width: 100vw;
    height: 75vw; /* height:width ratio = 3/4 = .5625  */
    max-height: 100vh;
    max-width: 133.33vh; /* 4/3 = 1.778 */
}
.appwrapper-4-to-3-landscape-inner {
    width: 100%;
    padding-bottom: 75%;
    font-size: 1.333vh;
    position: relative;
}
.appwrapper-4-to-3-portrait-inner {
    width: 100%;
    padding-bottom: 75%;
    font-size: 1vw;
    position: relative;
}

.appwrapper {
    text-align: center;
}
.appwrapper-jail {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

